@import url(https://fonts.googleapis.com/css?family=Supermercado+One|ZCOOL+XiaoWei&display=swap);
body {
  margin: 0;
  background: #000000;
  color: #ffffff;
  font-family: 'ZCOOL XiaoWei', serif;
}

h1.title {
  font-family: 'Supermercado One', cursive;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

div.card-col {
  height: 175px;
  margin-bottom: 15px;
}

div.card img {
  cursor: pointer;
  border: 2px solid #ffffff;
}

